@import url(./__paragraph/footer__paragraph.css);

.footer {
  padding-top: 66px;
  max-width: 882px;
  min-width: 300px;
  margin: 0 auto;
}

@media screen and (max-width: 920px) {
  .footer {
    width: calc(100% - 38px);
  }
}

@media screen and (max-width: 750px) {
  .footer {
    padding-top: 48px;
  }
}

@media screen and (max-width: 400px) {
  .footer {
    width: 100%;
  }
}

