.popup__container {
  background-color: #fff;
  width: 430px;
  margin: auto;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
  border-radius: 10px;
  position: relative;
  display: flex;
  flex-direction: column;
}

@media screen and (max-width: 550px) {
  .popup__container {
    width: 282px;
  }
}
